<template>
  <el-card class="box-card">
    <el-button type="primary" @click="$router.push('/admin/main/delivery/add-payload')">Load drone</el-button>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="id">
        <el-input placeholder="id" v-model="param.id"></el-input>
      </el-form-item>
      <el-form-item label="tag">
        <el-input placeholder="tag" v-model="param.tag"></el-input>
      </el-form-item>
      <el-form-item label="barcode">
        <el-input placeholder="barcode" v-model="param.barcode"></el-input>
      </el-form-item>
      <el-form-item label="quantity">
        <el-input placeholder="quantity" v-model="param.quantity"></el-input>
      </el-form-item>
      <el-form-item label="price">
        <el-input placeholder="price" v-model="param.price"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="id"
          prop="id"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="tag"
          prop="tag"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="barcode"
          prop="barcode"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="quantity"
          prop="quantity"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="price"
          prop="price"
          sortable
      >
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
    >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        id: '',
        tag: '',
        barcode: '',
        quantity: '',
        price: '',
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList() {
      this.$axios.post('/payload/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm() {
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e) {
      this.pageInfo.pageNum = e
      this.getTableList()
    }
  },
}
</script>

<style scoped lang="scss">
.demo-form-inline, .el-pagination {
  margin-top: 10px;
}
</style>

